/* GENERAL BUTTON STYLING */
.btn-diagonal {
  background: none;
  border: 3px solid #fff;
  border-radius: 5px;
  color: red;
  display: block;
  position: relative;
  text-transform: uppercase;
  overflow: hidden;
}
.btn-diagonal::after {
  transition: all 0.3s;
  height: 100%;
  left: -35%;
  top: 0;
  transform: skew(50deg);
  transition-duration: 0.6s;
  transform-origin: top left;
  width: 0;
}
.btn-diagonal::before, .btn-diagonal::after {
  background: red;
  content: "";
  position: absolute;
  z-index: -1;
}
.btn-diagonal:hover {
  color: #2ecc71;
}
.btn-diagonal:hover:after {
  height: 100%;
  width: 135%;
}

