footer {
  padding: 3% 0;
  position: relative;
}

.footer-text {
  color: inherit;
  font-family: "Roboto", sans-serif;
  font-size: 0.9vw;
  margin-bottom: 1rem;
  font-weight: 300;
}

.clip-footer-home {
  clip-path: polygon(0 33%, 100% 0, 100% 100%, 0% 100%);
}

div.map-responsive > iframe {
