section#slider-custom {
  min-width: 100%;
  max-height: 60rem;
  overflow: hidden;
}
section#slider-custom > div {
  min-width: 100%;
  max-height: inherit;
  overflow: hidden;
}
section#slider-custom > div > div.caroufredsel_wrapper {
  min-width: 100%;
  max-height: inherit;
  overflow: hidden;
}
section#slider-custom > div > div.caroufredsel_wrapper > ul.super-simple-slider {
  max-height: inherit;
  /* comeando links */
}
section#slider-custom > div > div.caroufredsel_wrapper > ul.super-simple-slider > li.slide {
  max-height: inherit;
}
section#slider-custom > div > div.caroufredsel_wrapper > ul.super-simple-slider > li.slide > .overlay-container {
  max-height: inherit;
}
section#slider-custom > div > div.caroufredsel_wrapper > ul.super-simple-slider > li.slide > .overlay-container > .overlay {
  max-height: inherit;
}
section#slider-custom > div > div.caroufredsel_wrapper > ul.super-simple-slider > li.slide > .overlay-container > .overlay > .opacity {
  max-height: inherit;
  height: 100% !important;
  padding: 0 !important;
  width: 100% !important;
  background-color: transparent !important;
}
section#slider-custom > div > div.caroufredsel_wrapper > ul.super-simple-slider > li.slide > .overlay-container > .overlay > .opacity > .buttons {
  max-height: inherit;
  width: 100% !important;
  height: 100% !important;
}
section#slider-custom > div > div.caroufredsel_wrapper > ul.super-simple-slider > li.slide > .overlay-container > .overlay > .opacity > .buttons > button {
  max-height: inherit;
  width: 100% !important;
  background: transparent !important;
  color: transparent !important;
  height: 100% !important;
  margin: 0 !important;
  padding: 0 !important;
}

@media (max-width: 600px) {
  section#slider-custom {
    max-width: none !important;
    height: 15rem !important;
    max-height: none !important;
    overflow: hidden !important;
    width: auto !important;
  }
  section#slider-custom > div {
    max-width: inherit !important;
    height: inherit !important;
    max-height: inherit !important;
    overflow: inherit !important;
    width: inherit !important;
  }
  section#slider-custom > div > div.caroufredsel_wrapper {
    max-width: inherit !important;
    height: inherit !important;
    max-height: inherit !important;
    overflow: inherit !important;
    width: inherit !important;
    display: flex;
    justify-content: center;
    position: absolute !important;
    inset: inherit !important;
  }
  section#slider-custom > div > div.caroufredsel_wrapper > ul {
    max-width: inherit !important;
    height: inherit !important;
    max-height: inherit !important;
    overflow: inherit !important;
    width: inherit !important;
    left: -50% !important;
  }
  section#slider-custom > div > div.caroufredsel_wrapper > ul > li {
    max-width: inherit !important;
    height: inherit !important;
    max-height: inherit !important;
    overflow: inherit !important;
    width: inherit !important;
  }
  section#slider-custom > div > div.caroufredsel_wrapper > ul > li > img {
    max-width: inherit !important;
    height: inherit !important;
    max-height: inherit !important;
    overflow: inherit !important;
    width: inherit !important;
  }
}
